"use client";

import { createContext, ReactNode, RefObject, useContext, useRef } from "react";
import { Socket } from "socket.io-client";
import { ClientToServerEvents, ServerToClientEvents } from "./types/game";

interface ContextType {
  SocketRef: RefObject<Socket<
    ServerToClientEvents,
    ClientToServerEvents
  > | null>;
}

const Context = createContext<ContextType | undefined>(undefined);

export const Provider = ({ children }: { children: ReactNode }) => {
  const SocketRef = useRef<Socket<
    ServerToClientEvents,
    ClientToServerEvents
  > | null>(null);

  return <Context.Provider value={{ SocketRef }}>{children}</Context.Provider>;
};

export const useGlobalContext = () => {
  const context = useContext(Context);
  if (!context)
    throw new Error("useGlobalContext must be used within a Provider");
  return context;
};
